    height: 24px;
}

.humata-accordion-toolbar .button-small strong {
    font-weight: 700;
}

.humata-accordion-toolbar .button-small em {
    font-style: italic;
}

/* Intent card toggle button */
.humata-intent-toggle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    padding: 0;
    margin-right: 8px;
    background: transparent;
    border: 1px solid transparent;
    border-radius: 4px;
    cursor: pointer;
    color: #50575e;
    transition: background 0.15s, border-color 0.15s, color 0.15s;
}

.humata-intent-toggle:hover {
    background: #dcdcde;
    border-color: #c3c4c7;
    color: #1d2327;
}

.humata-intent-toggle:focus {
    outline: none;
    box-shadow: 0 0 0 2px #2271b1;
}

.humata-intent-toggle .dashicons {
    font-size: 18px;
    width: 18px;
    height: 18px;
}

